<template>
  <div class="g-inherit m-article">
    <HeaderBar class="m-tab" :needb="true" :tit="sessionName"></HeaderBar>
    <div class="m-chat-main">
      <div class='invalidHint' v-if='scene==="team" && teamInvalid'>
        {{`您已退出该${teamInfo && teamInfo.type==='normal' ? '讨论组':'群'}`}}
      </div>
      <chat-list
        type="session"
        :msglist="msglist"
        :userInfos="userInfos"
        :myInfo="myInfo"
        @msgs-loaded="msgsLoaded"
      ></chat-list>
      <chat-editor
        type="session"
        :scene="scene"
        :to="to"
        :invalid="teamInvalid || muteInTeam"
        :invalidHint="sendInvalidHint"
        :advancedTeam="teamInfo && teamInfo.type === 'advanced'"
      ></chat-editor>
    </div>
  </div>
</template>

<script>
import ChatEditor from './components/ChatEditor'
import ChatList from './components/ChatList'
import util from 'common/YXutils'
import pageUtil from 'common/YXutils/page'


export default {
  components: {
    ChatEditor,
    ChatList
  },
  // 进入该页面，文档被挂载
  mounted () {
    this.$store.dispatch('showLoading')
    // 此时设置当前会话
    pageUtil.scrollChatListDown()

    setTimeout(() => {
      this.$store.dispatch('hideLoading')
    }, 1000)

    // 获取群成员
    if(this.scene === 'team') {
      var teamMembers = this.$store.state.teamMembers[this.to]
      if (teamMembers === undefined || teamMembers.length < this.teamInfo.memberNum) {
        this.$store.dispatch('getTeamMembers', this.to)
      }
    }
  },
  updated () {
    pageUtil.scrollChatListDown()
  },
  // 离开该页面，此时重置当前会话
  destroyed () {
    this.$store.dispatch('resetCurrSession')
  },
  data () {
    return {
      leftBtnOptions: {
        backText: ' ',
        preventGoBack: true,
      }
    }
  },
  computed: {
    sessionId () {
      let sessionId = this.$route.params.sessionId
      return sessionId
    },
    sessionName () {
      let sessionId = this.sessionId
      let user = null
      if (/^p2p-/.test(sessionId)) {
        user = sessionId.replace(/^p2p-/, '')
        if (user === this.$store.state.userUID) {
          return '我的手机'
        } else if (this.isRobot) {
          return this.robotInfos[user].nick
        } else {
          let userInfo = this.userInfos[user] || {}
          const a= util.getFriendAlias(userInfo)
          return a 
        }
      } else if (/^team-/.test(sessionId)) {
        if (this.teamInfo) {
          // teamInfo中的人数为初始获取的值，在人员增减后不会及时更新，而teamMembers在人员增减后同步维护的人员信息
          var members = this.$store.state.teamMembers && this.$store.state.teamMembers[this.teamInfo.teamId]
          var memberCount = members && members.length
          return this.teamInfo.name + (memberCount ? `(${memberCount})` : '')
        } else {
          return  '群'
        }
      }
    },
    scene () {
      return this.sessionId && util.parseSession(this.sessionId).scene
    },
    to () {
      return this.sessionId &&  util.parseSession(this.sessionId).to
    },
    myInfo () {
      return this.$store.state.myInfo
    },
    userInfos () {
      return this.$store.state.userInfos
    },
    robotInfos () {
      return this.$store.state.robotInfos
    },
    msglist () {
      let msgs = this.$store.state.currSessionMsgs
      return msgs
    },
    teamInfo() {
      if (this.scene==='team') {
        var teamId =  this.sessionId.replace('team-','')
        return this.$store.state.teamlist.find(team=>{
          return team.teamId === teamId
        })
      } 
      return undefined
    },
    muteInTeam(){
      if(this.scene!=='team') return false
      var teamMembers = this.$store.state.teamMembers 
      var Members = teamMembers && teamMembers[this.teamInfo.teamId]
      var selfInTeam = Members && Members.find(item=>{
        return item.account === this.$store.state.userUID
      })
      return selfInTeam && selfInTeam.mute || false
    },
    teamInvalid() {
      if (this.scene==='team') {
        return !(this.teamInfo && this.teamInfo.validToCurrentUser)
      }
      return false
    }, 
    sendInvalidHint() {
      if (this.scene==='team' && this.teamInvalid) {
        return `您已不在该${this.teamInfo && this.teamInfo.type==='normal'? '讨论组':'群'}，不能发送消息`
      } else if (this.muteInTeam) {
        return '您已被禁言'
      }
      return '无权限发送消息'
    }
  },
  methods: {
    onClickBack () {
      // location.href = '#/contacts'
      window.stopPlayAudio && window.stopPlayAudio()
      window.history.go(-1)
    },
    msgsLoaded () {
      pageUtil.scrollChatListDown()
    },
    enterNameCard () {
      if (/^p2p-/.test(this.sessionId)) {
        let account = this.sessionId.replace(/^p2p-/, '')
        if (account === this.$store.state.userUID) {
          location.href = `#/general`
          return
        }
        location.href = `#/namecard/${account}`
      }
    },
    onTeamManageClick() {
      if (this.teamInfo && this.teamInfo.validToCurrentUser) {
        location.href = `#/teammanage/${this.teamInfo.teamId}`
      } else {
        this.$toast('您已退出该群')
      }
    },
    onHistoryClick() {
      if (this.scene!=='team' || (this.teamInfo && this.teamInfo.validToCurrentUser)) {
        location.href = `#/chathistory/${this.sessionId}`
      } else {
        this.$toast('您已退出该群')
      }
    }
  }
}
</script>
<style scoped lang="less">
.header-top-ppp{
  position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 10px 0;
    width: 100%;
    height: 3.6rem;
    overflow: hidden;
    z-index: 3;
}

.g-window .m-tab .m-tab-right{
  width: 5rem;
  right: -1rem;
}
.m-tab-right {
  display: flex;
  justify-content: flex-end;

  .icon-history, .icon-team {
    display: inline-block;
    margin-right: .8rem;
    width: 1.7rem;
    height: 1.7rem;
    background: url(http://yx-web.nos.netease.com/webdoc/h5/im/icons.png);
    background-size: 20rem;
    background-position: -5.8rem -11.3rem;
  }
  .icon-team{
    background-position: -7.9rem -11.3rem;
  }
}
.invalidHint {
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  background-color: bisque;
  color: burlywood;
}
</style>
<style lang="less">
@height_editor: 4rem;
/* 消息变量 */
@msg_bg_you: #5cacde;
@msg_tx_you: #fff;
@msg_bg_me: #e5f4ff;
@msg_tx_me: #666;
@color_tip_font: #ccc;
/* 顶部导航高度 */
@height_nav: 3.6rem;
@height_tab: 3.6rem;
@theme_background: #fffbf9;
@color_tip_background: #e0e0e0;
@color_chat_editor_background: #e5f4ff;
.u-icon-img img{
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}
 .m-main, .m-album, .m-article, .m-room {
    position: absolute;
    padding-top: @height_nav;
    display: block;
    box-sizing: border-box;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* 控制元素在移动设备上是否使用滚动回弹效果 */
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
    overflow-x: hidden;
  }
   .m-article {
     top: 0;
    background: @theme_background;
  }
  /* 二级页面的tab */
  .m-tab {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 10px 0;
    width: 100%;
    height: @height_tab;
    overflow: hidden;
    z-index: 3;
    .vux-header-left, .vux-header-right {
      top: 20px;
    }
    .m-tab-top {
      position: relative;
      padding: 0;
      font-size: 1.3rem;

      &.vux-button-group {
        margin: 4px auto;
      }
    }
    .m-tab-right {
      position: absolute;
      right: 0;
      top: 0.1rem;
      font-size: 0.8rem;
      text-align: right;
      width: 4rem;
    }
  }
   /* 会话列表 */
  .m-article-main, .m-chat-main {
    position: relative;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
.g-window .vux-header .m-tab-top {
  width: 80%;
  margin: 0 10%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* 聊天页面 */
  .m-chat-main {
    /* 聊天页面有输入框 */
    padding: 0 0 @height_editor 0;
    /* 聊天历史记录没有输入框 */
    &.m-chat-history {
      padding-bottom: 0;
    }
    .m-chat-list {
      position: relative;
      display: block;
      box-sizing: border-box;
      padding: 1rem 2%;
      width: 100%;
      -webkit-overflow-scrolling: touch;
      overflow-y: scroll;
      overflow-x: hidden;
    }
  }
  .m-chat-editor {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: @height_editor;
    background-color: @color_chat_editor_background;
  }
  /* 聊天按钮组件 */
  .m-chat-editor-main {
    position: relative;
    display: block;
    box-sizing: border-box;
    padding: 0;
    height: 100%;
    width: 100%;
    .u-editor-input {
      position: relative;
      display: inline-block;
      box-sizing: border-box;
      margin: 0;
      padding: 0.2rem;
      padding-right: 10rem;
      width: 100%;
      height: @height_editor;
      textarea {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        padding: 0.2rem;
        font-size: 1rem;
        width: 100%;
        height: 100%;
        text-align: left;
        border: 1px solid #ccc;
        border-radius: 0.4rem;
      }
      i {
        font-style: normal;
      }
      .u-btn-record {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        margin: 1rem 0 0;
        width: 100%;
        height: 1.9rem;
        line-height: 1.8rem;
        border: 1px solid #ccc;
        border-radius: 0.3rem;
        text-align: center;
        font-size: 1rem;
        &.recording {
          background: #ccc;
        }
      }
      .u-record-time {
        position: absolute;
        top: -2.5rem;
        left: 0;
        width: 4.2rem;
        height: 2.2rem;
        line-height: 1.8rem;
        text-align: center;
        color: #fff;
        font-style: normal;
        background: url(https://yx-web-nosdn.netease.im/quickhtml%2Fassets%2Fyunxin%2Fdefault%2Frecord-time.png) no-repeat;
        transform: translateX(3rem);
        &.with-close-btn {
          &:after {
            content: ' ';
            position: absolute;
            top: 0;
            right: -2rem;
            display: block;
            width: 2rem;
            height: 2rem;
            background: url(https://yx-web-nosdn.netease.im/quickhtml%2Fassets%2Fyunxin%2Fdefault%2Fstop-record.png) no-repeat;
          }
        }
      }
    }
    .u-editor-icons {
      position: absolute;
      display: inline-block;
      right: 0;
      top: 0;
      width: 10rem;
      height: @height_editor;
      .u-editor-icon {
        position: relative;
        display: inline-block;
        top: calc(@height_editor / 2 - 0.8);
        width: 1.6rem;
        height: 1.6rem;
        margin-left: 0.2rem;
        vertical-align: middle;
        input[type="file"] {
          position: absolute;
          display: inline-block;
          left: 0;
          top: 0;
          width: inherit;
          height: inherit;
          opacity: 0;
          font-size: 1rem;
        }
      }
    }
    .u-editor-send {
      position: relative;
      display: inline-block;
      top: calc(@height_editor / 2 - 0.8);
      margin-left: 0.2rem;
      width: 2.6rem;
      height: 1.6rem;
      line-height: 1.6rem;
      padding: 0.1rem 0.2rem;
      border-radius: 0.2rem;
      font-size: 0.9rem;
      color: #fff;
      background-color: #0091e4;
      text-align: center;
    }
  }
   /* 消息记录 */
  .u-msg {
    position: relative;
    padding: 0.2rem 0;
    &.session-chat {
      padding: 0.6rem 0;
      clear: both;
    }
    /* 用户头像 */
    .msg-head {
      position: relative;
      display: inline-block;
      top: 0.1rem;
      margin: 0;
      margin-right: 0.6rem;
      margin-left: 0.6rem;
      padding: 0;
      width: 2rem;
      height: 2rem;
      vertical-align: top;
      img {
        position: relative;
        display: inline-block;
        margin: 0;
        width: inherit;
        height: inherit;
      }
    }
    .msg-user {
      font-style: italic;
      color: #999;
      em {
        font-weight: normal;
        margin-right: 1rem;
      }
    }
    .msg-audio img{
      float: right;
      margin-right: 0.3rem !important;
    }
    .msg-text {
      position: relative;
      display: inline-block;
      max-width: 54%;
      min-height: 1.2rem;
      word-break: break-word;
      height: auto;
      line-height: 1.2rem;
      font-size: 0.9rem;
      border: none;
      img {
        position: relative;
        display: inline-block;
        margin: 0;
        padding: 0;
        max-width: 100%;
        vertical-align: bottom;
        &.emoji-small {
          width: 1.4rem;
          height: 1.4rem;
          vertical-align: bottom;
        }
        &.emoji-big {
          width: 3rem;
          height: 3rem;
          vertical-align: middle;
        }
        &.emoji-big {
          width: 4.8rem;
          height: 4.8rem;
          vertical-align: middle;
        }
      }
      embed, video {
        position: relative;
        display: inline-block;
        max-width: 100%;
        max-height: 8rem;
        vertical-align: bottom;
        background-color: #000;
        text-align: center;
        color: #fff;
      }
      &::before, &::after {
        content:' ';
        position: absolute;
        top: 0.8rem;
        border-top: 0.4rem solid transparent;
        border-bottom: 0.4rem solid transparent;
        width: 0;
        height: 0;
      }
      &.notify {
        max-width: 100%;
      }
      .button {
        margin: 0.1rem 0;
        padding: 0.1rem 0.2rem;
        border: 1px solid #fff;
        border-radius: 0.2rem;
        background-color: #e5f4ff;
        color: #666;
      }
    }
    .msg-failed {
      position: relative;
      float: right;
      margin-right: 0.2rem;
      vertical-align: top;
      font-size: 0.92rem;
      line-height: 2.0rem;
    }
    .msg-link {
      display: block;
      position: absolute;
      bottom: -2rem;
      right: 0;
      min-width: 4rem;
      min-height: 1.2rem;
      word-break: normal;
      height: auto;
      line-height: 1.2rem;
      font-size: 1.0rem;
      border: none;
      a {
        color: #0091e4;
        text-decoration: underline;
        float: right;
      }
    }
    &.session-chat .msg-text {
      padding: 0.4rem;
      margin-bottom: 0.8rem;
      border-radius: 0.4rem;
    }
    &.session-chat .msg-robot {
      min-width: 3rem;
      margin-bottom: 3rem;
    }
    &.session-chat.item-you {
      .msg-head, .msg-text {
        float: left;
      }
      .msg-text {
        background-color: @msg_bg_you;
        color: @msg_tx_you;
        &::before {
          right: 99%;
          border-right: 0.4rem solid @msg_bg_you;
        }
        &::after {
          background: none;
        }
      }
    }
    &.session-chat.item-me {
      .msg-head, .msg-text {
        float: right;
      }
      .msg-text {
        color: @msg_tx_me;
        background-color: @msg_bg_me;
        &::before {
          background: none;
        }
        &::after {
          left: 99%;
          border-left: 0.4rem solid @msg_bg_me;
        }
      }
    }
    &.item-time {
      font-size: 0.8rem;
      text-align: center;
      color: @color_tip_font;
    }
    &.item-tip {
      font-size: 0.8rem;
      text-align: center;
      .tip {
        margin: 0 auto;
        width: 10rem;
        padding: 0.1rem;
        background-color: @color_tip_background;
        border-radius: 0.4rem;
      }
    }
    .notification {
      font-size: 0.8rem;
      text-align: center;
      color: @color_tip_font;
    }
    .notify {
      color: #f50;
      font-size: 0.8rem;
    }
  }
</style>


